.projects {
  background-color: var(--white);
}

.projects-list {
  margin: 0 auto;
  max-width: 1300px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 1em 2em;
  background-color: var(--white);
}

.projects-list > * {
  flex: 1 0 33%;
}
