h1 {
  font-size: 50px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 900;
  letter-spacing: 0.0625em;
}

#about {
  background-color: var(--secondary-color);
  padding: 5em 2em;
  color: var(--white);
}

.home-about {
  height: 100%;
  width: 70%;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    "a c"
    "b c";
  column-gap: 2em;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 5em 1fr;
}

.home-about > h1 {
  margin: 0;
  grid-area: a;
  text-align: left;
  border-bottom: 10px solid var(--primary-color);
  padding-bottom: 10px;
}

.home-about > span {
  grid-area: b;
  font-family: Roboto, sans-serif;
  text-align: left;
  line-height: 1.3em;
  font-size: 20px;
  /* letter-spacing: 1px; */
}

.home-about > img {
  height: auto;
  width: fit-content;
  max-width: 300px;
  grid-area: c;
  margin: auto;
}

.home-projects > h1 {
  text-align: center;
  margin-bottom: 5px;
}

.home-projects > h3 {
  margin: 0;
  margin-bottom: 15px;
  text-align: center;
  font-family: "Source Pro Sans", Raleway, sans-serif;
  letter-spacing: 0.05em;
}

#home-project-container {
  margin: 0 auto;
  max-width: 1300px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 1em 2em;
}

#home-project-container > * {
  flex: 1 0 33%;
}

#projects-button {
  display: inline-block;
  color: black;
  border: 2px inset black;
  text-decoration: none;
  padding: 10px 15px;
  margin-bottom: 5%;
  background-color: #e7e7e7;
}

#projects-button:hover {
  background-color: #a7a7a7;
}

@media only screen and (max-width: 900px) {
  #about {
    padding: 5em 0;
  }

  .home-about {
    grid-template-areas:
      "a"
      "b"
      "c";
    column-gap: 0;
    grid-template-columns: 1fr;
  }

  .home-about > img {
    max-width: 100%;
  }
}
